var maxNum = 9,
	_this;
import unikModal from '@/components/unik-modal/unik-modal.vue';
export default {
	components: {
		unikModal
	},
	data() {
		return {
			imgLists: [],
			array: ['古钱币类', '书画类', '珠宝玉石类', '瓷器类', '杂项类'],
			canAdd: true,
			index: 0,
			expertName: '',
			tabbar: true,
			windowHeight: '',
			pic: [],
			title: '',
			wxTit: '',
			imageUrl: '',
			open: 0,
			// Ltype: '1',
			price: '50',
			content: '',
			actions: [],
			expertId: '',
			textAa: true,
			zhuanjiaShow: false,
			radioItems: [
				{
					value: '鉴定+估价',
					name: '2'
				},
				{
					value: '普通鉴定',
					checked: 'true',
					name: '1'
				}
			]
		};
	},
	onLoad() {
		uni.showShareMenu({
			withShareTicket: true
		});
		_this = this;
		this.$ajax
			.get({
				url: '/user/listMaster?status=0&masterId=0&pageNum=0&pageSize=0'
			})
			.then(res => {
				_this.actions = res.data.data;
				console.log(_this.actions);
			});
		this.$ajax
			.get({
				url: '/fenxiang/selectShare'
			})
			.then(res => {
				_this.wxTit = res.data.data.title;
				_this.imageUrl = res.data.data.url;
				// console.log(_this.actions);
			});
	},
	methods: {
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.index = e.target.value;

			if (this.index == 0) {
				this.price = '50';
			} else {
				this.price = '100';
			}
		},

		switch1Change: function(e) {
			console.log('switch1 发生 change 事件，携带值为', e.target.value);
			if (e.target.value == true) {
				this.open = 0;
			} else {
				this.open = 1;
			}
			console.log(this.open);
		},
		testAs1() {
			uni.hideTabBar();
			this.textAa = false;
			this.zhuanjiaShow = true;
		},
		showTabbar() {
			this.tabbar = true;
		},
		hideTabbar() {
			this.tabbar = false;
		},
		bindQualityChange(e) {
			this.index = e.target.value;
		},
		//上传图片
		addImg: function() {
			var num = maxNum - _this.imgLists.length;
			console.log(_this.imgLists.length);
			if (num < 1) {
				return false;
			}
			uni.chooseImage({
				count: num,
				sizeType: ['compressed'],
				success: function(res) {
					_this.imgLists = _this.imgLists.concat(res.tempFilePaths);
					if (_this.imgLists.length >= maxNum) {
						_this.canAdd = false;
					}
					for (let i = 0; i < res.tempFilePaths.length; i++) {
						uni.uploadFile({
							url: 'https://jianbao.mumonline.net/jianbao/upload/fileUploadInTencent',
							filePath: res.tempFilePaths[i],
							name: 'file',
							formData: {
								user: 'test'
							},
							success: function(uploadFileRes) {
								let data = JSON.parse(uploadFileRes.data);
								_this.pic.push(data.data.url);
							}
						});
					}
				},
				fail: function() {
					uni.hideLoading();
				}
			});
		},
		removeImg: function(e) {
			var index = e.currentTarget.id.replace('grace-items-img-', '');
			_this.imgLists.splice(index, 1);
			_this.pic.splice(index, 1);
			_this.imgLists = _this.imgLists;
			if (_this.imgLists.length < maxNum) {
				_this.canAdd = true;
			}
		},
		showImgs: function(e) {
			var currentImg = e.currentTarget.dataset.imgurl;
			uni.previewImage({
				urls: this.imgLists,
				current: currentImg
			});
		},
		reset() {
			this.title = '';
			this.content = '';
			this.index = 0;
		},
		radioChange: function(e) {
			var checked = e.target.value;
			this.Ltype = e.target.value;
			console.log(this.Ltype);
			var changed = {};
			for (var i = 0; i < this.radioItems.length; i++) {
				if (checked.indexOf(this.radioItems[i].name) !== -1) {
					changed['radioItems[' + i + '].checked'] = true;
				} else {
					changed['radioItems[' + i + '].checked'] = false;
				}
				// console.log(i)
			}
		},
		show() {
			this.$refs.unikModal.show();
			this.textAa = false;
		},
		confirmModal() {
			console.log('您点击了确定');
			this.textAa = true;
		},
		closeZhuanjia() {
			uni.showTabBar();
			this.zhuanjiaShow = false;
			this.textAa = true;
		},
		tapZhuanjia(item) {
			uni.showTabBar();
			console.log(item.id);
			this.expertName = item.expertName;
			this.zhuanjiaShow = false;
			this.textAa = true;
			this.expertId = item.id;
		},
		//生成订单
		Launch() {
			if (this.content == '' || this.imgLists.length == 0) {
				uni.showToast({ title: '请完善信息', icon: 'none' });
				return false;
			}
			let data = {
				// expertId: this.expertId,
				// jianbaoMethodId: this.Ltype,
				pic: this.pic,
				jianbaoType: this.array[this.index],
				isOpenStatus: this.open,
				productIntroduce: this.content,
				productName: this.title,
				userId: uni.getStorageSync('userId')
			};
			this.$ajax
				.postJSON({
					url: '/JianBao/create111',
					data: data
				})
				.then(res => {
					console.log(res);
					uni.showLoading({
						title: '加载中...',
						mask: true
					});
					// uni.showToast({ title: '添加成功', icon: 'success' });
					_this.did = res.data.data.id;
					_this.payy(_this.did);
				});
		},
		//支付
		payy(id) {
			uni.hideLoading();
			let _this = this;
			let data = {
				user_id: uni.getStorageSync('userId'),
				orderAmount: _this.price,
				body: '鉴宝服务',
				id: id
			};
			this.$ajax
				.post({
					url: '/wxpay/wxproPaymentCallback',
					data: data
				})
				.then(res => {
					this.expertName = '';
					this.title = '';
					this.content = '';
					let pays = res.data.data;
					console.log(pays);
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: pays.timeStamp,
						nonceStr: pays.nonceStr,
						package: pays.package,
						signType: pays.signType,
						paySign: pays.paySign,
						success: res => {
							uni.showToast({
								title: '支付成功'
							});
							setTimeout(function() {
								uni.switchTab({
									url: '../tabBar/home'
								});
							}, 800);
						},
						fail: err => {
							uni.hideLoading();
							uni.showToast({
								title: '取消支付',
								icon: 'none'
							});
							(this.expertId = ''), this.stepIndex--;
						}
					});
				});
		},
		onShareAppMessage: function(ops) {
			if (ops.from === 'button') {
				// 来自页面内转发按钮
				console.log(ops.target);
			}
			return {
				title: this.wxTit,
				path: 'pages/tabBar/home',
				imageUrl: this.imageUrl,
				success: function(res) {
					// 转发成功
					console.log('转发成功:' + JSON.stringify(res));
				},
				fail: function(res) {
					// 转发失败
					console.log('转发失败:' + JSON.stringify(res));
				}
			};
		}
	}
};
